:root {
  --bg:#0A1109;
  --color-title:#C0C0C0;
  --color-text:#FFFFFF;
  --main-color:#00C056;
  --card-color:#082B15;
  --bg-nav:rgba(192, 192, 192, 0.2);
  --box-shadow:0px 0px 20px 0px var(--main-color);
  --drop-shadow:-4px 4px 8px var(--main-color);
  --links-color:#000000;
}

:root {
  --font-family-2: "Oleo Script Swash Caps", cursive;
  --font-family-1: "Poppins", sans-serif;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--font-family-1);
  color: var(--color-text);
  background: var(--bg);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
  position: relative;
}

.artistic-text {
  font-family: var(--font-family-2);
  background: linear-gradient(var(--main-color) 35%, var(--bg) 88%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.artistic-text--responsive {
  font-size: 16vw;
  line-height: 100%;
}
@media (min-width: 40rem) {
  .artistic-text--responsive {
    font-size: 5rem;
  }
}
@media (min-width: 71.875rem) {
  .artistic-text--responsive {
    font-size: 7.5rem;
  }
}

h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

form, input, textarea,
select, button, label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  display: block;
  color: inherit;
}

.container {
  max-width: 87.5rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.8125rem;
}
@media (min-width: 71.875rem) {
  .container {
    padding: 0 2rem;
  }
}

.sections__section {
  min-height: 100vh;
}

.header {
  transition: 0.3s ease-in-out;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  transform: scale(0.95);
  opacity: 0;
  width: 100%;
  height: 100vh;
  background: var(--bg);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  padding: 3rem 1rem;
  pointer-events: none;
  z-index: 1;
}
@media (min-width: 56.25rem) {
  .header {
    opacity: 1;
    pointer-events: auto;
    height: 4rem;
    width: 100%;
    border-radius: 0 0 1rem 1rem;
    flex-direction: row;
    gap: 0;
    justify-content: space-between;
    transform: scale(1);
    background: var(--card-hover);
    backdrop-filter: blur(0.8rem);
  }
}
.header__logo {
  max-width: 5rem;
  opacity: 0;
  animation: disappear-animation 0.3s forwards;
}
@media (min-width: 56.25rem) {
  .header__logo {
    animation: none;
    opacity: 1;
  }
}
.header__ssmm {
  width: 100%;
  opacity: 0;
  animation: disappear-animation 0.5s forwards;
}
@media (min-width: 56.25rem) {
  .header__ssmm {
    animation: none;
    opacity: 1;
  }
}
.header__nav {
  width: 100%;
}
.header.open {
  transform: scale(1);
  opacity: 1;
  pointer-events: auto;
}
.header.open .nav__li {
  animation: appear-animation 0.5s forwards 0.5s;
}
.header.open .nav__li:nth-child(2) {
  animation: appear-animation 0.5s forwards 0.6s;
}
.header.open .nav__li:nth-child(3) {
  animation: appear-animation 0.5s forwards 0.7s;
}
.header.open .nav__li:nth-child(4) {
  animation: appear-animation 0.5s forwards 0.8s;
}
.header.open .nav__li:nth-child(5) {
  animation: appear-animation 0.5s forwards 0.9s;
}
.header.open .header__logo {
  animation: appear-animation 0.5s forwards 0.5s;
}
.header.open .header__ssmm {
  animation: appear-animation 0.5s forwards 1s;
}

.nav__ul {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 56.25rem) {
  .nav__ul {
    animation: none;
    opacity: 1;
    flex-direction: row;
  }
}
.nav__li {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 0;
  opacity: 0;
  width: 100%;
  animation: disappear-animation 0.5s forwards;
}
@media (min-width: 56.25rem) {
  .nav__li {
    animation: none;
    opacity: 1;
    width: auto;
  }
}
.nav__a {
  width: 100%;
  padding: 1.5rem 2rem;
  font-size: 1rem;
  text-align: center;
  transition: 0.3s ease-in-out;
}
@media (min-width: 56.25rem) {
  .nav__a {
    width: auto;
    padding: 1.5rem 1rem;
  }
}
.nav__a:hover {
  color: var(--main-color);
}
.nav__a--active {
  color: var(--main-color);
  font-weight: 700;
}

@keyframes appear-animation {
  0% {
    transform: translatey(-0.75rem);
    opacity: 0;
  }
  100% {
    transform: translatey(0px);
    opacity: 1;
  }
}
@keyframes disappear-animation {
  0% {
    transform: translatey(0px);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.ssmm {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 0.5rem;
  padding: 2.5rem 0;
  border-top: 0.125rem solid var(--color-theme);
}
@media (min-width: 56.25rem) {
  .ssmm {
    border-top: none;
    justify-content: flex-end;
    width: 12rem;
    padding: 1rem 0;
  }
}
.ssmm__svg {
  max-height: 1.875rem;
  stroke: var(--color-text);
  transition: 0.3s ease-in-out;
}
.ssmm__svg:hover {
  stroke: var(--main-color);
}
.toggler {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 3.75rem;
  height: 3.75rem;
  border-radius: 50%;
  margin: 0 1.5rem 1.5rem 0;
  border: 2px solid var(--main-color);
  box-shadow: var(--box-shadow);
  background: var(--bg);
  z-index: 2;
  transition: 0.4s ease-in-out;
}
@media (min-width: 56.25rem) {
  .toggler {
    opacity: 0;
    pointer-events: none;
  }
}

.toggler.close {
  background: var(--main-color);
}

.burger {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.burger__line {
  position: absolute;
  width: 60%;
  height: 0.3rem;
  background-color: var(--color-text);
  border-radius: 0.25rem;
  box-shadow: var(--box-shadow);
}

.line-one {
  width: 35%;
  animation: line-one-animation 0.6s forwards;
}

.line-two {
  animation: line-two-animation 0.6s forwards;
}

.line-three {
  width: 35%;
  animation: line-three-animation 0.6s forwards;
}

.burger.close .line-one {
  animation: line-one-close-animation 0.6s forwards;
}

.burger.close .line-two {
  animation: line-two-close-animation 0.6s forwards;
}

.burger.close .line-three {
  animation: line-three-close-animation 0.6s forwards;
}

@keyframes line-one-close-animation {
  0% {
    transform: translatey(-0.75rem);
  }
  50% {
    transform: translatey(0px);
  }
  100% {
    width: 50%;
    transform: rotate(45deg);
  }
}
@keyframes line-two-close-animation {
  0% {
    transform: scale(100%);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes line-three-close-animation {
  0% {
    transform: translatey(0.75rem);
  }
  50% {
    transform: translatey(0px) rotate(0deg);
  }
  100% {
    width: 50%;
    transform: rotate(135deg);
  }
}
@keyframes line-one-animation {
  0% {
    width: 50%;
    transform: rotate(45deg);
  }
  50% {
    transform: translatey(0px) rotate(0deg);
  }
  100% {
    width: 35%;
    transform: translatey(-0.75rem);
  }
}
@keyframes line-two-animation {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(100%);
  }
}
@keyframes line-three-animation {
  0% {
    width: 50%;
    transform: rotate(135deg);
  }
  50% {
    transform: translatey(0px) rotate(0deg);
  }
  100% {
    width: 35%;
    transform: translatey(0.75rem);
  }
}
.btn {
  font-size: 1.125rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  line-height: 1.125rem;
  background: var(--main-color);
  border-radius: 2rem;
  padding: 0.75rem 2.25rem;
  margin-top: 2rem;
  transition: 0.3s ease-in-out;
}
.btn__svg {
  max-width: 1.5rem;
}

.btn:hover {
  box-shadow: var(--box-shadow);
  transform: scale(1.02);
}
.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  justify-content: center;
}
@media (min-width: 40rem) {
  .cards {
    grid-template-columns: 1fr 1fr;
    column-gap: 1.8rem;
  }
}
@media (min-width: 71.875rem) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.8rem;
  }
}
.cards .card {
  border-radius: 2rem;
  min-width: 18rem;
  overflow: hidden;
  position: relative;
  transition: all 0.4s;
  width: 100%;
  background: var(--card-color);
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: stretch;
  gap: 0;
}
@media (min-width: 56.25rem) {
  .cards .card:hover .card__cover {
    transition: 0.4s;
    width: fit-content;
    height: 100%;
    scale: 1;
  }
  .cards .card:hover .card__content {
    transition: 0.4s;
    opacity: 1;
  }
}
.cards .card__cover {
  transition: 0.4s;
  object-fit: cover;
  height: 100%;
  width: fit-content;
}
@media (min-width: 56.25rem) {
  .cards .card__cover {
    scale: 1.4;
  }
}
.cards .card__content {
  transition: 0.4s;
  padding: 1rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 56.25rem) {
  .cards .card__content {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    height: 100%;
    opacity: 0;
  }
}
.cards .card__title {
  font-size: 1.2rem;
}
.cards .card__techs {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 0;
}
.cards .card__description {
  font-size: 0.9rem;
  font-weight: 100;
}
@media (min-width: 56.25rem) {
  .cards .card__description {
    font-size: 1rem;
  }
}
.cards .card__tech {
  font-weight: 100;
  color: var(--main-color);
}
.cards .card__links {
  width: 100%;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 56.25rem) {
  .cards .card__links {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }
}
.cards .card__link {
  margin-top: 0;
  font-size: 1rem;
  width: 100%;
  padding: 0.8rem 1.2rem;
}
@media (min-width: 56.25rem) {
  .cards .card__link {
    width: auto;
  }
}

.shape {
  display: none;
  position: fixed;
  z-index: -1;
  opacity: 0.1;
  filter: blur(0.5rem);
  height: 100vh;
  width: 300vw;
  object-fit: cover;
  object-position: 75%;
  transition: 0.4s ease-in-out;
}
@media (min-width: 40rem) {
  .shape {
    width: 100%;
    filter: blur(1rem);
    object-position: center;
    opacity: 0.4;
    mix-blend-mode: lighten;
  }
}

@media (min-width: 40rem) {
  .home {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    gap: 0;
  }
}
.home__content {
  height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  gap: 0;
}
@media (min-width: 40rem) {
  .home__content {
    flex: 1 1 150%;
  }
}
@media (min-width: 71.875rem) {
  .home__content {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-start;
    gap: 0;
    flex: 1 1 90%;
  }
}
.home__content-span {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
.home__content-h1 {
  font-size: 16vw;
  line-height: 100%;
  background-image: linear-gradient(to right, #00C056, #0ece64, #2ad677, #48ef93, #24ad86, #32cba0, #40c9a2, #00C056);
  background-size: 400% auto;
  animation: move 8s linear infinite;
  animation-delay: 3s;
}
@media (min-width: 40rem) {
  .home__content-h1 {
    font-size: 6.8rem;
  }
}
@media (min-width: 71.875rem) {
  .home__content-h1 {
    font-size: 7.5rem;
  }
}
@keyframes move {
  to {
    background-position: 400% center;
  }
}
.home__content-h2 {
  font-size: 7.3vw;
  font-weight: 700;
}
@media (min-width: 40rem) {
  .home__content-h2 {
    font-size: 3rem;
  }
}
@media (min-width: 71.875rem) {
  .home__content-h2 {
    font-size: 3.3rem;
  }
}
.home__content-p {
  font-size: 1rem;
  line-height: 1.5rem;
  margin-top: 1rem;
  font-style: normal;
  font-weight: 400;
}
@media (min-width: 71.875rem) {
  .home__content-p {
    font-size: 1.125rem;
    margin-top: 3rem;
  }
}
.home__content-p--2 {
  font-weight: 200;
  font-style: italic;
}
@media (min-width: 71.875rem) {
  .home__content-p--2 {
    font-size: 1.125rem;
  }
}
.home__content-studying {
  width: 100%;
  margin-top: 2rem;
}
@media (min-width: 40rem) {
  .home__content-studying {
    display: none;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(175deg);
  }
}
.home__content-learningtech {
  max-width: 5rem;
  margin: 2rem auto 0;
  animation: rotate 6s ease-in-out infinite alternate;
}
.home__cover-img {
  max-width: fit-content;
  width: 100%;
}
@media (min-width: 71.875rem) {
  .home__cover-img {
    max-width: fit-content;
    width: 32rem;
  }
}

@media (min-width: 40rem) {
  .about__content {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }
}
@media (min-width: 71.875rem) {
  .about__content {
    gap: 4rem;
  }
}
.about__img {
  max-width: 25rem;
  min-width: 18rem;
  margin: 0 auto;
}
@media (min-width: 40rem) {
  .about__img {
    max-width: 30rem;
  }
}
.about__p {
  font-size: 1rem;
  line-height: 1.125rem;
  margin-bottom: 1rem;
  font-weight: 200;
}
.skills {
  margin-top: 4rem;
}
.skills__cards {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}
.skills__card {
  border-radius: 1rem;
  background: var(--card-color);
  max-width: 7rem;
  padding: 1rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 71.875rem) {
  .skills__card {
    max-width: 8rem;
  }
}
.services {
  margin-top: 3rem;
}
@media (min-width: 40rem) {
  .services {
    min-height: auto;
  }
}
.services__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  justify-content: center;
}
@media (min-width: 56.25rem) {
  .services__list {
    grid-template-columns: 1fr 1fr;
    column-gap: 1.8rem;
  }
}
@media (min-width: 71.875rem) {
  .services__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.8rem;
  }
}
.services__item {
  border-radius: 2rem;
  background: var(--card-color);
}
.services__content {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: start;
  gap: 1rem;
  padding: 2rem;
  width: 100%;
  height: 100%;
  position: relative;
}
.services__icon {
  font-size: 2rem;
}
.services__title {
  font-weight: 700;
  font-size: 1.2rem;
  color: var(--main-color);
}
.services__description {
  font-weight: 100;
}

.portfolio {
  margin-top: 3rem;
}
.footer {
  width: 100%;
  margin-top: 3rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  transition: 0.3s ease-in-out;
}
@media (min-width: 40rem) {
  .footer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    gap: 0;
    height: 3rem;
    width: 100%;
    border-radius: 0 0 1rem 1rem;
    padding: 3rem 0;
    border-radius: 1rem;
  }
}
.footer__title {
  font-size: 5rem;
  line-height: 4.5rem;
}
@media (min-width: 40rem) {
  .footer__title {
    font-size: 4rem;
    line-height: 3.5rem;
  }
}
.footer__mail {
  font-weight: 100;
  font-size: 1rem;
  transition: 0.3s ease-in-out;
}
.footer__mail:hover {
  font-weight: 700;
  color: var(--main-color);
}